<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getAdmins"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd"></el-button>
				</el-form-item>

			</el-form>
		</el-col>

		<!---->
		<template>
			<el-table :data="users" highlight-current-row v-loading="loading" style="width: 100%;"
					  stripe
					  border>
				<el-table-column type="index" width="60" align="center" >
				</el-table-column>
				<el-table-column prop="adminInfo.userId" label=""  sortable align="center" >
				</el-table-column>
				<el-table-column prop="adminInfo.realname" label=""  align="center" >
				</el-table-column>
				<el-table-column prop="adminInfo.username" label=""  align="center" >
				</el-table-column>
				<el-table-column prop="adminInfo.phonenumber" label=""  align="center" >
				</el-table-column>
				<el-table-column prop="adminGroupAuthority.adminGroupName" label="" align="center" >
				</el-table-column>
				<el-table-column inline-template :context="_self" label="" width="200" align="center" >
					<span>
						<el-button size="small" @click="handleEdit(row)"></el-button>
						<el-button type="danger" size="small" @click="handleDel(row)"></el-button>
					</span>
				</el-table-column>
			</el-table>
		</template>
		<el-col :span="24" class="toolbar" style="padding-bottom:10px;">
			<el-pagination layout="total,prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="this.total" style="float:right;">
			</el-pagination>
		</el-col>
		<!---->
		<el-dialog :title="editFormTtile" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm" :label-position="labelPosition">
				<el-form-item label=""  prop="realname">
					<el-input v-model="editForm.realname" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label=""  prop="username">
					<el-input v-model="editForm.username" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="password">
					<el-input v-model="editForm.password" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="phonenumber">
					<el-input v-model="editForm.phonenumber" ></el-input>
				</el-form-item>
				<el-form-item label="" prop="authorityName">
					<el-radio-group v-model="editForm.authorityName ">
						<el-radio label=""></el-radio>
						<el-radio label=""></el-radio>
						<el-radio label=""></el-radio>
						<el-radio label=""></el-radio>
					</el-radio-group>
				</el-form-item>

			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"> </el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading">{{btnEditText}}</el-button>
			</div>
		</el-dialog>

	</section>
</template>
<script>
    import { getUserInfoById , getAllAdminInfo , deleteAdminInfoById , addAdminInfo , updateAdminInfo } from '../../api/api';
    import NProgress from 'nprogress'
    export default {
        data() {
            return {
                filters: {
                    name: ''
                },
                labelPosition: 'left',
                editForm: {
                    userId:'',
                    username:'',
                    password:'',
                    realname:'',
                    userGroupId:'',
                    phonenumber:'',
                    authorityName:''
                },
                total: 0,
                page: 1,
                loading: false,
                editFormVisible: false,//
                editFormTtile: '',//
                editLoading: false,
                editFormRules: {
                    password: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    username: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    realname: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    phonenumber: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    authorityName: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                },
                btnEditText:'',
                users: [],
            }
        },
        mounted(){
            this.getAdmins();
        },
        methods: {
            handleCurrentChange(val) {
                this.page = val;
                this.getAdmins();
            },
            //
            getAdmins(){
                let param = {
                    sort: "user_id",
                    order: "asc",
                    offset: (this.page - 1) * 10,
                    limit: 10,
                    adminInfo:{
                        username:this.filters.name,
                    }
                };
                this.loading = true;
                NProgress.start();
                getAllAdminInfo(param).then((res) => {
                    if(res.status === 1){
                        this.total = res.result.count;
                        this.users = res.result.adminInfos;
                        this.loading = false;
                        NProgress.done();
                    }

                });
            },
            //
            handleAdd: function (){
                this.editFormTtile = '';
                this.editForm.userId = -1;
                this.editForm.username = '';
                this.editForm.realname = '';
                this.editForm.password = '';
                this.editForm.phonenumber = '';
                this.editForm.authorityName = '';
                this.editFormVisible = true;
            },
            //
            handleDel: function (row) {
                // console.log(row);
                // console.log(row.userId);
                var _this = this;
                this.$confirm('?', '', {
                    //type: 'warning'
                }).then(() => {
                    _this.Loading = true;
                    NProgress.start();
                    let param = new FormData();
                    param.append("userId", row.adminInfo.userId);
                    deleteAdminInfoById(param).then((res) => {
                        if(res.status === 1){
                            _this.Loading = false;
                            NProgress.done();
                            _this.$notify({
                                title: '',
                                message: '',
                                type: 'success'
                            });
                            _this.getAdmins();
                        }
                        else
                        {
                            _this.Loading = false;
                            NProgress.done();
                            this.$message({
                                type: 'error',
                                message: ''+res.result
                            });
                        }
                    });

                }).catch((res) => {
                    _this.Loading= false;
                    NProgress.done();
                    _this.$notify({
                        title: '',
                        message: '',
                        type: 'error'
                    });
                });
            },
            //
            handleEdit: function (row) {
                //console.log("row,",row);
                this.editFormTtile = '';
                this.editForm.userId = row.adminInfo.userId;
                this.editForm.username = row.adminInfo.username;
                this.editForm.realname = row.adminInfo.realname;
                this.editForm.password = row.adminInfo.password;
                this.editForm.phonenumber = row.adminInfo.phonenumber;
                this.editForm.authorityName = row.adminGroupAuthority.adminGroupName;
                this.editFormVisible = true;
            },
            //
            editSubmit: function () {
                var _this = this;
                _this.$refs.editForm.validate((valid) => {
                    if (valid) {
                        _this.$confirm('', '', {}).then(() => {
                            _this.editLoading = true;
                            NProgress.start();
                            _this.btnEditText = '';
                            if (_this.editForm.userId === -1) {
                                //
                                var authorityId ;
                                if(_this.editForm.authorityName == "")
                                {
                                    authorityId=1;
                                }
                                else if(_this.editForm.authorityName == "")
                                {
                                    authorityId=2;
                                }
                                else if(_this.editForm.authorityName == "")
                                {
                                    authorityId=3;
                                }
                               else if(_this.editForm.authorityName == "")
                                {
                                    authorityId=4;
                                }
                                let param = {
                                    username: _this.editForm.username,
                                    realname: _this.editForm.realname,
                                    phonenumber: _this.editForm.phonenumber,
                                    password: _this.editForm.password,
                                    authorityId : authorityId,
                                };
                                addAdminInfo(param).then((res) => {
                                    if(res.status === 1){
                                        _this.editLoading = false;
                                        NProgress.done();
                                        _this.btnEditText = ' ';
                                        _this.$notify({
                                            title: '',
                                            message: '',
                                            type: 'success'
                                        });
                                        _this.editFormVisible = false;
                                        _this.getAdmins();
                                    }
                                    else
                                    {
                                        _this.btnEditText = ' ';
                                        _this.editLoading = false;
                                        NProgress.done();
                                        this.$message({
                                            type: 'error',
                                            message: ''+res.result
                                        });
                                    }
                                });
                            } else {
                                //
                                var authorityId ;
                                if(_this.editForm.authorityName == "")
                                {
                                    authorityId=1;
                                }
                                else if(_this.editForm.authorityName == "")
                                {
                                    authorityId=2;
                                }
                                else if(_this.editForm.authorityName == "")
                                {
                                    authorityId=3;
                                }
                                else if(_this.editForm.authorityName == "")
                                {
                                    authorityId=4;
                                }
                                let param = {
                                    userId:_this.editForm.userId,
                                    username: _this.editForm.username,
                                    realname: _this.editForm.realname,
                                    phonenumber: _this.editForm.phonenumber,
                                    password: _this.editForm.password,
                                    authorityId: authorityId,
                                };
                                updateAdminInfo(param).then((res) => {
                                    // console.log("!!!",res)
                                    if(res.status === 1){
                                        _this.editLoading = false;
                                        NProgress.done();
                                        _this.btnEditText = ' ';
                                        _this.$notify({
                                            title: '',
                                            message: '',
                                            type: 'success'
                                        });
                                        _this.editFormVisible = false;
                                        _this.getAdmins();
                                    }
                                    else
                                    {
                                        _this.btnEditText = ' ';
                                        _this.editLoading = false;
                                        NProgress.done();
                                        this.$message({
                                            type: 'error',
                                            message: ''+res.result
                                        });
                                    }

                                });

                            }

                        }).catch((res) => {
                            _this.editLoading= false;
                            NProgress.done();
                            _this.$notify({
                                title: '',
                                message: '',
                                type: 'error'
                            });
                        });

                    }
                });

            },
        }

    };
</script>

<style scoped>

</style>


