<template>
	<section>
		<el-col :span="30" class="toolbar" style="float:left">
			<el-form :inline="true" :model="filters">
                <el-form-item  style="float:right">
                    <el-button type="primary" v-on:click="getExcel"></el-button>
                </el-form-item>
				<el-form-item>
					<el-input v-model="filters.driverName" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>

				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.driverPhoneNumber" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>

				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.driverLevelStar" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getDriver"></el-button>
				</el-form-item>
				<el-form-item >
				<el-upload
						class="upload-demo"
						ref="upload"
						action="http://localhost:8000/admin/driver/importExcel"
						:on-preview="handlePreview"
						:on-remove="handleRemove"
                        :on-change="handleChange"
						:before-upload="beforeUpload"
                        :auto-upload="false"
						:limit="1"
						:file-list="fileList">
					<el-button  slot="trigger" size="small" type="primary"></el-button>
                    <el-button  style="margin-left: 10px" size="small" type="success" @click ="submitUpload"></el-button>
					<span slot="tip" class="el-upload__tip">Excel</span>
				</el-upload>
				</el-form-item>
			</el-form>
		</el-col>


		<!---->
		<el-table :data="driverInfo"
				  style="width: 100% ;margin: 10px 0 10px 0"
				  stripe
				  border
				  v-loading="listLoading">
			<el-table-column type="index" width="60">
			</el-table-column>
			<el-table-column prop="driverInfo.driverStatus" label="" align="center"  sortable  >
				<template slot-scope="scope" >
					<div style="text-algin:center;margin:auto">
						<i v-if="scope.row.driverInfo.driverStatus==0"  style="color:#F56C6C;"></i>
						<i v-else-if="scope.row.driverInfo.driverStatus==1"  style="color:#67C23A;"> </i>
					</div>
				</template>
			</el-table-column>
			<el-table-column prop="driverInfo.driverName" label="" align="center"    >
			</el-table-column>
			<!--<el-table-column prop="driverInfo.driverWxId" label="" align="center"  width="150" >-->
			<!--</el-table-column>-->
			<el-table-column prop="driverInfo.driverPhoneNumber" label=""  align="center"   >
			</el-table-column>
			<el-table-column prop="driverInfo.driverIdentity" label=""  align="center"   >
			</el-table-column>
			<!--<el-table-column prop="driverInfo.driverLicence" label=""  align="center"   >-->
			<!--</el-table-column>-->
			<el-table-column prop="driverInfo.driverLevelStar" label=""  align="center"  sortable  >
			</el-table-column>
			<el-table-column  label=""  align="center"   >
				<el-table-column prop="carInfo.carNumber" label=""  align="center"   >
				</el-table-column>
				<el-table-column prop="carInfo.carType" label=""  align="center"  sortable >
				</el-table-column>
				<el-table-column prop="carInfo.carSeat" label=""  align="center"  sortable >
				</el-table-column>
                <el-table-column inline-template :context="_self" label=""   align="center">
	             <span>
					<el-button size="small" @click="changeCar(row)"></el-button>
				</span>
                </el-table-column>
			</el-table-column>

			<el-table-column inline-template :context="_self" label=""  width="180px" align="center">
	        <span>
                <el-button size="small" @click="handleEdit(row)"></el-button>
                <el-button type="danger" size="small" @click="handleDel(row)"></el-button>
            </span>
			</el-table-column>
		</el-table>

			<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom:10px;">
			<el-pagination layout="total,prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="this.total" style="float:right;">
			</el-pagination>
		</el-col>

		<!---->
		<el-dialog :title="editFormTtile" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="" prop="driverName">
					<el-input v-model="editForm.driverName" auto-complete="off"></el-input>
				</el-form-item>
				<!--<el-form-item label=""  prop="driverWxId">-->
					<!--<el-input v-model="editForm.driverWxId"  size="medium"></el-input>-->
				<!--</el-form-item>-->
				<el-form-item label=""  prop="driverPhoneNumber">
					<el-input  v-model="editForm.driverPhoneNumber" ></el-input>
				</el-form-item>
				<el-form-item label=""  prop="driverIdentity">
					<el-input  v-model="editForm.driverIdentity"></el-input>
				</el-form-item>
				<!--<el-form-item label=""  prop="driverLicence">-->
					<!--<el-input  v-model="editForm.driverLicence"></el-input>-->
				<!--</el-form-item>-->
				<el-form-item label=""  prop="driverLevelStar">
				<el-input v-model="editForm.driverLevelStar"></el-input>
			</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"> </el-button>
				<el-button type="primary" @click.native="editSubmit2" :loading="editLoading">{{btnEditText}}</el-button>
			</div>
		</el-dialog>
        <!---->
        <el-dialog title="" v-model="editFormVisible1" :close-on-click-modal="false" width = "30%" :visible.sync="dialogVisible">
            <el-row v-loading = "editLoading1">
            <el-form :model="editForm1" :rules="editFormRules1" ref="editForm1" label-width="100px">
                <el-form-item label="">

                <el-col :span="5">
                    <el-input v-model="carForm.carNumber" placeholder="" ></el-input>
                </el-col>
					<el-col class="line" :span="2">&nbsp;</el-col>
					<el-button type="primary" v-on:click="getCar"></el-button>
            </el-form-item>
                <el-form-item label="Id" prop="carId">
                    <el-col :span="5">
                    <el-input v-model="editForm1.carId"  style="width: 100%;" disabled></el-input>
                    </el-col>
                </el-form-item>
                <el-form-item label=""  prop="carNumber">
                    <el-col :span="5">
                    <el-input v-model="editForm1.carNumber"  style="width: 100%;" disabled></el-input>
                    </el-col>
                </el-form-item>
                <el-form-item label=""  prop="carType">
                    <el-col :span="5">
                    <el-input  v-model="editForm1.carType" style="width: 100%;" disabled></el-input>
                    </el-col>
                </el-form-item>
                <el-form-item label=""  prop="carSeat">
                    <el-col :span="5">
                    <el-input  v-model="editForm1.carSeat" style="width: 100%;" disabled></el-input>
                    </el-col>
                </el-form-item>
				<el-form-item label=""  prop="carDriverIdADriverName">
					<el-col :span="5">
						<el-input  v-model="carDriverIdADriverName" style="width: 100%;" disabled></el-input>
					</el-col>
					<el-col class="line" :span="2">&nbsp;</el-col>
					<el-button type="primary" v-if="carDriverIdADriverName!=''" v-on:click="getCarOffA"></el-button>
					<el-button   type="primary" v-else v-on:click="getCarOffA" disabled></el-button>
				</el-form-item>
				<el-form-item  prop="carDriverIdBDriverName">
					<el-col :span="5">
						<el-input  v-model="carDriverIdBDriverName" style="width: 100%;" disabled></el-input>

					</el-col>
					<el-col class="line" :span="2">&nbsp;</el-col>
					<el-button type="primary" v-if="carDriverIdBDriverName!=''" v-on:click="getCarOffB"></el-button>
					<el-button   type="primary" v-else v-on:click="getCarOffB" disabled></el-button>
				</el-form-item>
				<!--<span v-if="editForm1.carDriverIdBDriverName!=''&&editForm1.carDriverIdADriverName!=''">-->

				<!--</span>-->
            </el-form>
            </el-row>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="editFormVisible1 = false"> </el-button >
                <el-button type="primary" @click.native="editSubmit1()" :loading="editLoading1">{{btnEditText1}}</el-button>
            </div>
        </el-dialog>
	</section>
</template>

<script>
    import NProgress from 'nprogress'
    import {getCarOff, getDriverInfoByDriverId,getDriverInfoByMultiCondition,updateDriverInfoByDriverId,deleteDriverInfoByDriverId ,importExcel,makeExcel,getCarInfoNoCompatibleByCarNumber,changCar} from '../../api/api';
    export default {
        data() {
            return {
                fileList: [],
                filters: {
                    driverName:"",
                    driverPhoneNumber:"",
                    driverLevelStar:""
                },
                carForm: {
                    carNumber:""
                },
                carDriverIdADriverName:'',
                driverIdA:0,
                driverIdB:0,
                carDriverIdBDriverName:'',
                driverInfo: [],
                total: 0,
                page: 1,
                loading:false,
                dialogVisible:false,
                listLoading: false,
                editFormVisible: false,//
                editFormTtile: '',//
                editFormVisible1: false,//
                editFormTtile1: '',//
                //
                editForm: {
                    id: 0,
                    driverName:"",
                    driverWxId:"",
                    driverPhoneNumber:"",
                    driverIdentity:"",
                    driverLicence:"",
                    driverLevelStar:"",
                },
                //
                editForm1: {
                    id: 0,
                    driverId:"",
                    carId:"",
                    carNumber:"",
                    carType:"",
                    carSeat:"",
                    carDriverIdA:"",
                    carDriverIdB:"",
                     driverName:"",
                },
                editLoading: false,
                editLoading1: false,
                btnEditText1: ' ',
                btnEditText: ' ',
                editFormRules: {
                    name: [
                        { required: true, message: '', trigger: 'blur' }
                    ]
                },
                editFormRules1: {
                    name: [
                        { required: true, message: '', trigger: 'blur' }
                    ]
                }

            }
        },
        methods: {
            submitUpload: function () {
                this.$refs.upload.submit();
            },
            handleRemove(file, fileList) {
                console.log(file, fileList);
            },
            handlePreview(file) {
                console.log(file);
            },
            handleChange(file, fileList) {
                console.log(file);
                console.log(fileList);
            },
            beforeUpload: function (file) {
                console.log(file)
                var testmsg = file.name.substring(file.name.lastIndexOf('.') + 1)
                const extension = testmsg === 'xls'
                const extension2 = testmsg === 'xlsx'
                const isLt2M = file.size / 1024 / 1024 < 10
                if (!extension && !extension2) {
                    this.$message({
                        message: ' xlsxlsx!',
                        type: 'warning'
                    });
                }
                else if (!isLt2M) {
                    this.$message({
                        message: ' 10MB!',
                        type: 'warning'
                    });
                }
                else {
                    //formdata
                    let fd = new FormData()
                    fd.append('file', file)
                    importExcel(fd).then((res) => {
                        if (res.status === 1) {
                        this.$notify({
                            title: '',
                            message: '',
                            type: 'success'
                        });
                        this.getDriver();
                        }
                        else
                        {
                            this.$message({
                                type: 'error',
                                message: ''+res.result
                            });
                        }
                    });
                }
                return false;
            },

            handleCurrentChange(val) {
                this.page = val;
                this.getDriver();
            },
            getDriver: function () {
                    let param = {
                        sort: "driver_id",
                        order: "asc",
                        offset: (this.page - 1) * 10,
                        limit: 10,
                        driverInfo: {
                            driverName: this.filters.driverName,
                            driverPhoneNumber: this.filters.driverPhoneNumber,
                            driverLevelStar: this.filters.driverLevelStar
                        }
                    }
                    this.listLoading = true;
                NProgress.start();
                getDriverInfoByMultiCondition(param).then((res) => {
                    if (res.status === 1) {
                        this.total = res.result.count;
                        this.driverInfo = res.result.driverInfos;
                        this.listLoading = false;
                        NProgress.done();
                    }
                });
            },
            getCar: function () {
                this.carDriverIdADriverName='';
				this.driverIdA=0;
                this.driverIdB=0;
				this.carDriverIdBDriverName='';
                var driverId = this.editForm1.driverId;
                var driverName = this.editForm1.driverName;
                this.editLoading1 = true;
                NProgress.start();
                if (this.carForm.carNumber == "") {
                    this.editLoading1 = false;
                    this.$notify.error({
                        title: '',
                        message: "",
                        type: 'error'
                    });
                    NProgress.done();
                    this.carForm.carNumber = '';
                }
                else if(this.carForm.carNumber == this.editForm1.carNumber)
				{
                    this.editLoading1 = false;
                    this.$notify.error({
                        title: '',
                        message: "",
                        type: 'error'
                    });
                    this.carForm.carNumber = '';
                    NProgress.done();
				}
                else {
                    getCarInfoNoCompatibleByCarNumber(this.carForm.carNumber)
                        .then((res) => {
                            if (res.status === 1) {
                                this.editLoading1 = false;
                                this.editForm1 = res.result;
                                this.editForm1.driverId = driverId;
                                this.editForm1.driverName = driverName;
                                if(res.result.carDriverIdA != 0 )
								{
                                    getDriverInfoByDriverId(this.editForm1.carDriverIdA )
                                        .then((res) => {
                                            if (res.status === 1) {
                                                this.carDriverIdADriverName = res.result.driverName;
                                                this.driverIdA = res.result.driverId;
                                            }
                                        }
										)
								}
								else
                                    this.editForm1.carDriverIdADriverName ="";
                                if(res.result.carDriverIdB != 0  )
                                {
                                    getDriverInfoByDriverId(this.editForm1.carDriverIdB )
                                        .then((res) => {
                                                if (res.status === 1) {
                                                    this.editLoading1 = false;
                                                    this.carDriverIdBDriverName = res.result.driverName;
                                                    this.driverIdB = res.result.driverId;
                                                }
                                            }
                                        )
                                }
                                else
                                    this.editForm1.carDriverIdBDriverName ="";
                                this.editLoading1 = false;
                                this.$notify({
                                    title: '',
                                    message: '',
                                    type: 'success'
                                });
                                NProgress.done();
                            }
                            else {
                                this.editLoading1 = false;
                                this.carForm.carNumber = '';
                                this.$notify.error({
                                    title: '',
                                    message: "",
                                    type: 'error'
                                });
                            }
                        })
                }
            },
            getExcel() {
                this.$confirm('', '', {}).then(() => {
                        NProgress.start();
                        this.btnEditText = '';
                makeExcel().then((response) => {
                        this.$notify({
                            title: '',
                            message: 'D',
                            type: 'success'
                        });
                    NProgress.done();
                    }).catch((error) => {
                    this.$message.error(error.data.message);
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'error'
                    });
                    NProgress.done();
                })
					}).catch(() => {
                    NProgress.done();
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'error'
                    });
                });
            },
            // //
            handleDel: function (row) {
                var _this = this;
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    _this.listLoading = true;
                    NProgress.start();
                    let param = new FormData();
                    param.append("driverId", row.driverInfo.driverId);
                    deleteDriverInfoByDriverId(param).then((res) => {
                        if (res.status === 1) {
                            _this.listLoading = false;
                            NProgress.done();
                            _this.$notify({
                                title: '',
                                message: '',
                                type: 'success'
                            });
                            _this.getDriver();
                        }
                        else
                        {
                            _this.listLoading = false;
                            NProgress.done();
                            this.$message({
                                type: 'error',
                                message: ''+res.result
                            });
                        }
                    });
                }).catch(() => {
                    _this.listLoading = false;
                    NProgress.done();
                    _this.$notify({
                        title: '',
                        message: '',
                        type: 'error'
                    });
                });
            },
            getCarOffA: function () {
                var _this = this;
                _this.$refs.editForm1.validate((valid) => {
                        if (valid) {
                            _this.$confirm('', '', {}).then(() => {
                                _this.editLoading1 = true;
                                NProgress.start();
                                _this.btnEditText = '';
                                let param = {
                                    driverId: _this.driverIdA,
                                    carId: _this.editForm1.carId
                                };
                                getCarOff(param).then((res) => {
                                    if (res.status === 1) {
                                        _this.editLoading1 = false;
                                        this.carDriverIdADriverName=''
                                        NProgress.done();
                                        _this.btnEditText = ' ';
                                        _this.$notify({
                                            title: '',
                                            message: '',
                                            type: 'success'
                                        });
                                    }
                                    else
                                    {
                                        _this.editLoading1 = false;
                                        NProgress.done();
                                        this.$message({
                                            type: 'error',
                                            message: ''+res.result
                                        });
                                    }
                                });
                            }).catch((res) => {
                                _this.editLoading1 = false;
                                NProgress.done();
                                _this.$notify({
                                    title: '',
                                    message: '',
                                    type: 'error'
                                });
							});
                        }
                        else {
                            _this.$notify({
                                title: '',
                                message: '',
                                type: 'error'
                            });
                        }
                });
            },
            getCarOffB: function () {
                var _this = this;
                _this.$refs.editForm1.validate((valid) => {
                    if (valid) {
                        _this.$confirm('', '', {}).then(() => {
                            _this.editLoading1 = true;
                            NProgress.start();
                            _this.btnEditText = '';
                            let param = {
                                driverId: _this.driverIdB,
                                carId: _this.editForm1.carId
                            };
                            getCarOff(param).then((res) => {
                                if (res.status === 1) {
                                    this.carDriverIdBDriverName='';
                                    _this.editLoading1 = false;
                                    NProgress.done();
                                    _this.btnEditText = ' ';
                                    _this.$notify({
                                        title: '',
                                        message: '',
                                        type: 'success'
                                    });
                                }
                                else
                                {
                                    _this.editLoading1 = false;
                                    NProgress.done();
                                    this.$message({
                                        type: 'error',
                                        message: ''+res.result
                                    });
                                }
                            });
                        }).catch((res) => {
                            _this.editLoading1 = false;
                            NProgress.done();
                            _this.$notify({
                                title: '',
                                message: '',
                                type: 'error'
                            });
                        });
                    }
                    else {
                        _this.$notify({
                            title: '',
                            message: '',
                            type: 'error'
                        });
                    }
                });
            },
            //
            handleEdit: function (row) {
                this.editFormVisible = true;
                this.editFormTtile = '';
                this.editForm.id = row.id;
                this.editForm.driverId = row.driverInfo.driverId;
                this.editForm.driverName = row.driverInfo.driverName;
                this.editForm.driverPhoneNumber = row.driverInfo.driverPhoneNumber;
                //this.editForm.driverWxId = row.driverInfo.driverWxId;
                this.editForm.driverIdentity = row.driverInfo.driverIdentity;
                //this.editForm.driverLicence = row.driverInfo.driverLicence;
                this.editForm.driverLevelStar = row.driverInfo.driverLevelStar;
            },
            //
            changeCar: function (row) {
                this.editFormVisible1 = true;
                 this.editForm1.driverId = row.driverInfo.driverId;
                 this.editForm1.driverName = row.driverInfo.driverName;
                 this.editForm1.id = row.id;
				 this.editForm1.carId = '';
				 this.editForm1.carNumber = '';
				 this.editForm1.carType = '';
				 this.editForm1.carSeat ='';
                 this.carDriverIdADriverName = '';
                 this.carDriverIdBDriverName = '';
                 this.carForm.carNumber ='';
            },
            editSubmit1: function () {
                var _this = this;
                _this.$refs.editForm1.validate((valid) => {
                    if(this.carForm.carNumber == "") {
                        _this.$notify({
                            title: '',
                            message: ',',
                            type: 'error'
                        });
                    }
                    else if(this.carDriverIdADriverName!=''&&this.carDriverIdBDriverName!='')
					{
                        _this.$notify({
                            title: '',
                            message: '',
                            type: 'error'
                        });
					}
					else if(this.carDriverIdADriverName == this.editForm1.driverName ||this.carDriverIdBDriverName == this.editForm1.driverName)
                    {
                        _this.$notify({
                            title: '',
                            message: '',
                            type: 'error'
                        });
                    }
                    else{
                    if (valid) {
                        _this.$confirm('', '', {}).then(() => {
                            _this.editLoading1 = true;
                            NProgress.start();
                            _this.btnEditText = '';
                            let param = {
                                driverId: _this.editForm1.driverId,
                                carId: _this.editForm1.carId
                            };
                            changCar(param).then((res) => {
                                if (res.status === 1) {
                                    _this.editLoading1 = false;
                                    NProgress.done();
                                    _this.btnEditText = ' ';
                                    _this.$notify({
                                        title: '',
                                        message: '',
                                        type: 'success'
                                    });
                                    this.carForm.carNumber = '';
                                    _this.carDriverIdADriverName='';
									_this.carDriverIdBDriverName='';
                                    _this.editFormVisible1 = false;
                                    _this.getDriver();
                                }
                                else
                                {
                                    _this.editLoading1 = false;
                                    NProgress.done();
                                    this.$message({
                                        type: 'error',
                                        message: ''+res.result
                                    });
                                }
                            });
                        }).catch((res) => {
                            _this.editLoading1 = false;
                            NProgress.done();
                            _this.$notify({
                                title: '',
                                message: '',
                                type: 'error'
                            });
                        });
                        }
					}
                });
            },
            editSubmit2: function () {
                var _this = this;
                _this.$refs.editForm.validate((valid) => {
                    if (valid) {
                        if( _this.editForm.driverId == '' ||_this.editForm.driverName == '' ||
							_this.editForm.driverPhoneNumber == '' ||
							_this.editForm.driverIdentity == ''  ||_this.editForm.driverLevelStar == ''){
                            _this.$notify({
                                title: '',
                                message: '',
                                type: 'error'
                            });
						}
						else{
                            _this.$confirm('', '', {}).then(() => {
                            _this.editLoading = true;
                            NProgress.start();
                            _this.btnEditText = '';
                            //
                            let param = {
                                driverId: _this.editForm.driverId,
                                driverName: _this.editForm.driverName,
                              // driverWxId: _this.editForm.driverWxId,
                                driverPhoneNumber: _this.editForm.driverPhoneNumber,
                                driverIdentity: _this.editForm.driverIdentity,
                               // driverLicence: _this.editForm.driverLicence,
                                driverLevelStar: _this.editForm.driverLevelStar,
                            };
                            updateDriverInfoByDriverId(param).then((res) => {
                                if (res.status === 1) {
                                    _this.editLoading = false;
                                    NProgress.done();
                                    _this.btnEditText = ' ';
                                    _this.$notify({
                                        title: '',
                                        message: '',
                                        type: 'success'
                                    });
                                    _this.editFormVisible = false;
                                    _this.getDriver();
                                }
                                else
                                {
                                    _this.btnEditText = ' ';
                                    _this.editLoading = false;
                                    NProgress.done();
                                    this.$message({
                                        type: 'error',
                                        message: ''+res.result
                                    });
                                }
                            });
                        }).catch((res) => {
                            _this.editLoading= false;
                            NProgress.done();
                            _this.$notify({
                                title: '',
                                message: '',
                                type: 'error'
                            });
                        });
                    }}
                });
            }
        },
        mounted() {
            this.getDriver();
        }
    }
</script>

<style scoped>
    .uptemp .el-upload-list {
        position: absolute;
        left: 140px;
        top: 0;
        width: 50%;
    }

    .uptemp {
        position: relative;
    }

    .uptemp .el-upload-list .el-upload-list__item {
        margin-top: 0;
    }
</style>


