<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar">
			<el-form :inline="true" :model="filters">
				<el-form-item label="">
					<el-col :span="11">
						<el-date-picker type="date" placeholder="" v-model="trackDate.startYmd" style="width: 100%;" ></el-date-picker>
					</el-col>
					<el-col class="line" :span="2">&nbsp;&nbsp;&nbsp;&nbsp;-</el-col>
					<el-col :span="11">
						<el-time-picker type="fixed-time" placeholder="" v-model="trackDate.startHms" style="width: 100%;"></el-time-picker>
					</el-col>
				</el-form-item>
				<el-form-item label="">
					<el-col :span="11">
						<el-date-picker type="date" placeholder=""  v-model="trackDate.endYmd" style="width: 100%;"></el-date-picker>
					</el-col>
					<el-col class="line" :span="2">&nbsp;&nbsp;&nbsp;&nbsp;-</el-col>
					<el-col :span="11">
						<el-time-picker type="fixed-time" placeholder=""  v-model="trackDate.endHms" style="width: 100%;"></el-time-picker>
					</el-col>
				</el-form-item><br>
				<el-form-item>
					<el-input v-model="formData.logInfo.logId" type="number" placeholder="()"></el-input>
				</el-form-item>
				<el-form-item>
					<el-input v-model="formData.logInfo.action" placeholder=""></el-input>
				</el-form-item>
				<el-select v-model="formData.logInfo.roleId" placeholder="" clearable>
					<el-option
					v-for="item in roleData"
					:key="item.roleId"
					:label="item.roleName"
					:value="item.roleId">
					</el-option>
				</el-select>
				<el-form-item>
					<el-input v-model="formData.logInfo.orderId" type="number" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="findLogInfo"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<template>
			<el-table :data="infolist" highlight-current-row v-loading="listLoading" style="width: 100%;" stripe
					  border>
				<el-table-column type="index" width="60">
				</el-table-column>
				<el-table-column prop="logId" label="" sortable  align="center">
				</el-table-column>
				<el-table-column prop="action" label="" sortable  align="center">
				</el-table-column>
				<el-table-column prop="roleId" label="" :formatter="formatRole" sortable  align="center">
				</el-table-column>
				<el-table-column prop="logTime" label="" :formatter="formatTime" sortable  align="center">
				</el-table-column>
				<el-table-column prop="orderId" label=""  sortable  align="center">
				</el-table-column>
				<el-table-column inline-template :context="_self" label=""  align="center">
					<span>
						<el-button size="small" @click="handleEdit(row)"></el-button>
					</span>
				</el-table-column>
			</el-table>
		</template>

<!---->
<el-col :span="24" class="toolbar" style="padding-bottom:10px;">
<el-pagination layout="total,prev, pager, next" @current-change="handleCurrentChange" :page-size="filters.limit" :total="total" style="float:right;">
</el-pagination>
</el-col>

<!---->
<el-dialog :title="editFormTtile" v-model="editFormVisible" :close-on-click-modal="false">
	<el-form :model="editForm" label-width="80px" ref="editForm">
		<el-form-item label="">
			<el-input v-model="editForm.logId" auto-complete="off" disabled ></el-input>
		</el-form-item>
		<el-form-item label="">
			<el-input v-model="editForm.orderId" auto-complete="off" disabled></el-input>
		</el-form-item>
		<el-form-item label="">
			<el-input v-model="editForm.action" auto-complete="off" disabled></el-input>
		</el-form-item>
		<el-form-item label="">
			<el-input v-model="editForm.roleId" auto-complete="off" disabled></el-input>
		</el-form-item>
		<el-form-item label="">
			<el-input v-model="editForm.logTime" auto-complete="off" disabled></el-input>
		</el-form-item>
	</el-form>
	<div slot="footer" class="dialog-footer">
		<el-button @click.native="editFormVisible = false"> </el-button>
	</div>
</el-dialog>
</section>
</template>

<script>
	import util from '../../common/util'
	import NProgress from 'nprogress'
	import { findLogInfoMultiCondition, getLogInfoByLogId} from '../../api/api';
	import { formatDate } from '../../common/util'

	export default {
		data() {
			return {
				roleData: [{
					roleId: 1,
          			roleName: ''
				},{
					roleId: 2,
          			roleName: ''
				}],
				formData: {
					logInfo: {
						logId: '',
						action: '',
						roleId: '',
						logTime: '',
						orderId: '',
					},
				},
				filters: {
					offset: 0,
					sort: 'log_id',
					order: 'desc',
					limit:  10,
					startTime: '',
					endTime: '',
					logInfo: {
						logId: '',
						action: '',
						roleId: '',
						logTime: '',
						orderId: '',
					},
				},
				trackDate: {
					startYmd: null,
					startHms: null,
					endYmd: null,
					endHms: null
            	},
				infolist: [],
				total: 0,
				page: 1,
				listLoading: false,
				editFormVisible: false,//
				editFormTtile: '',//
				//
				editForm: {
					logId: '',
					action: '',
					roleId: '',
					logTime: '',
					orderId: '',
				},
				editLoading: false,
				btnEditText: ' ',
				editFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
				}

			}
		},
		methods: {
			//
			formatRole: function (row, column) {
				return row.roleId == 1 ? '' : row.roleId == 2 ? '' : '';
			},


			formatTime: function(row, column) {
				var d = new Date(row.logTime);
				var times = d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate() + ' ' + d.getHours() + ':' + d.getMinutes() + ':' + d.getSeconds();
				return times;
				
			},

			formatDate (date, flag) {
				if (date != null){
                    var date = new Date(date);
					if(flag===0){//
						let y = date.getFullYear();
						let m = date.getMonth() + 1;
						let d = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
						return `${y}-${m}-${d}`
					}else{//
                        var date = new Date(date);
						let h = date.getHours() < 10 ? "0" + date.getHours() : date.getHours()
						let m = date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
						let s = date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
						return `${h}:${m}:${s}`
					}
				}
				return null;
			},

			handleCurrentChange(val) {
				console.log("page", val)
				this.page = val;
				console.log("page", this.page)
				this.getLogInfoList();
			},
			//
			getLogInfoList() {
				let para = {
					offset: (this.page - 1) * 10,
					sort: this.filters.sort,
					order: this.filters.order,
					limit: this.filters.limit,
					// startTime: start,
					// endTime: end,
					logInfo: {
						logId: this.filters.logInfo.logId,
						action: this.filters.logInfo.action,
						roleId: this.filters.logInfo.roleId,
						logTime: this.filters.logInfo.logTime,
						orderId: this.filters.logInfo.orderId,
					},
				};
				this.listLoading = true;
				NProgress.start();
				findLogInfoMultiCondition(para).then((res) => {
					this.total = res.result.count;
					this.infolist = res.result.logInfoList;
					this.listLoading = false;
					NProgress.done();
				});
			},
			//
			findLogInfo() {
                var start='';
                var end = '';
                if(this.trackDate.startYmd!=null&&this.trackDate.startYmd!='') {
                     start = this.formatDate(this.trackDate.startYmd, 0) + " " + this.formatDate(this.trackDate.startHms, 1)
                    if (start == 'null null') {
                        start = ''
                    }
                     end = this.formatDate(this.trackDate.endYmd, 0) + " " + this.formatDate(this.trackDate.endHms, 1)
                    if (end == 'null null') {
                        end = ''
                    }
                }  else{
                    start = '';
                    end = this.formatDate(this.trackDate.endYmd, 0) + " " + this.formatDate(this.trackDate.endHms, 1)
                    if (end == 'null null') {
                        end = ''
                    }
                }
				let para = {
					offset: (this.page - 1) * 10,
					sort: this.filters.sort,
					order: this.filters.order,
					limit: this.filters.limit,
					startTime: start,
					endTime: end,
					logInfo: {
						logId: this.formData.logInfo.logId,
						action: this.formData.logInfo.action,
						roleId: this.formData.logInfo.roleId,
						logTime: this.formData.logInfo.logTime,
						orderId: this.formData.logInfo.orderId,
					},
				};
				this.listLoading = true;
				NProgress.start();
				findLogInfoMultiCondition(para).then((res) => {
					this.total = res.result.count;
					this.infolist = res.result.logInfoList;
					this.listLoading = false;
					NProgress.done();
				});
			},
			//
			handleEdit: function (row) {
				
				console.log("row",row.logId)
				var param = new FormData()
				param.append('logInfoId', row.logId)
				getLogInfoByLogId(param).then((res) => {
					if(res.status === 1){
                    console.log(res.result)
					this.editFormVisible = true;
					this.editFormTtile = '';
					this.editForm.logId = row.logId;
					this.editForm.action = row.action;
					if(row.roleId==1){
						this.editForm.roleId = '';
					} else {
						this.editForm.roleId = '';
					}
					var d = new Date(row.logTime);
					var times = d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate() + ' ' + d.getHours() + ':' + d.getMinutes() + ':' + d.getSeconds();
					this.editForm.logTime = times;
					this.editForm.orderId = row.orderId;
					}
				});	
			},
			// or 
			editSubmit: function (row) {
				let param = {
                carNumber: this.carNumber
            	}
				getLogInfoByLogId(para).then((res) => {
					if(res.status === 1){
                    console.log(res.result)
					}
				});
			},
		},
		mounted() {
			this.getLogInfoList();
		}
	}
</script>

<style scoped>
</style>


